import React from 'react'
import { useHistory, useLocation } from 'react-router-dom'
import './index.scss'
const tabs = [
  {
    name: '推荐',
    path: '/recommend'
  },
  {
    name: '歌手',
    path: '/singers'
  },
  {
    name: '排行',
    path: '/rank'
  },
  {
    name: '搜索',
    path: '/search'
  }
]
function MuTab() {
  let history = useHistory()
  let location = useLocation()
  function handleClick(path) {
    history.push(path)
  }
  return (
    <div className='Mu-menus'>
      {tabs.map((tab, index) => (
        <div
          className={[
            'Mu-menus-item',
            location.pathname === tab.path ? 'Mu-menus-item--active' : ''
          ].join(' ')}
          key={index}
        >
          <span onClick={() => handleClick(tab.path)}>{tab.name}</span>
        </div>
      ))}
    </div>
  )
}

export default MuTab
