import React, { Fragment } from 'react'
import './type-card.scss'
import rank from '../../assets/images/rank.jpeg'

function TypeCard(props) {
  const { list } = props
  return (
    <Fragment>
      <img className="Mu-rank-pic" src={rank} alt="图片"/>
      <ul className="Mu-rank-list">
        {
          list.map((item, index) => (
            <li key={index}>{index + 1} {item.song} - {item.singer}</li>
          ))
        }
      </ul>
    </Fragment>
  )
}

export default TypeCard