import React, { useEffect } from 'react'
import BScroll from 'better-scroll'
import TypeCard from './type-card'
import './index.scss'
import { useHistory } from 'react-router-dom'

const Ranks = [
  {
    pic: '', // 排行榜图片
    id: 1,
    list: [
      {
        singer: 'Justin Bieber',
        song: 'Yummy'
      },
      {
        singer: '戴羽彤',
        song: '星空下的我'
      },
      {
        singer: '谢宇伦',
        song: '凌晨两点的北京'
      }
    ]
  },
  {
    pic: '', // 排行榜图片
    id: 2,
    list: [
      {
        singer: '要不要买菜',
        song: '下山'
      },
      {
        singer: 'G.E.M 邓紫棋',
        song: '句号'
      },
      {
        singer: 'G.E.M 邓紫棋',
        song: '透明'
      }
    ]
  }
]

function SortCards() {
  let el = null
  const history = useHistory()

  function handleClick() {
    console.log(123);
    history.push('/rank/1')
  }

  useEffect(() => {
    new BScroll(el, {
      click: true
    })
    return () => {
      // cleanup
    };
  }, [el])

  return (
    <div className="Mu-sortcards">
      <div className="Mu-sortcards-wrap" ref={ref => el = ref}>
        <ul>
          {
            Ranks.map((rank, index) => (
              <li className="Mu-sortcards-card" onClick={() => handleClick()} key={index}>
                <TypeCard list={rank.list} />
              </li>
            ))
          }
        </ul>
      </div>
    </div>
  )
}

export default SortCards