import types from './types'

export const togglePlayerStatus = status =>  ({
  type: 'TOGGLE_STATUS',
  payload: {
    status
  }
})

export const addTodo = content => ({
  type: 'ADD_TODO',
  payload: { content }
})
/**
 * 设置流行榜列表是否在顶部的标志
 * @param {Boolean} status 状态值
 */
export const toggleIsReachTop = status => ({
  type: 'TOGGLE_IS_REACH_TOP',
  payload: { status }
})
/**
 * 设置音乐播放器状态
 * @param {boolean} status 播放状态
 */
export const togglePlayingStatus = status => ({
  type: types.player.TOGGLE_PLAYING_STATUS,
  payload: { playing: status }
})