import React, { useState, useEffect, useRef } from 'react'
import { useHistory } from 'react-router'
import { CSSTransition } from 'react-transition-group'
import { connect } from 'react-redux'
import { togglePlayingStatus } from '../../store/actionCreator'
import juhao from '../../assets/句号-G.E.M.邓紫棋.mp3'
// import velocity from 'velocity-animate'
// import animejs from 'animejs'
import './index.scss'

import bg from '../../assets/images/image-bg.jpeg'

function Player(props) {
  const dateRef = useRef()
  const cdRef = useRef()
  const audio = useRef()
  const history = useHistory()
  const [show, setShow] = useState(false)
  const [mode, setMode] = useState(1) // 1 顺序模式 2 随机模式 3 循环模式
  const { playing, togglePlayingStatus } = props

  function enter(el) {
    cdRef.current.classList.add('ani')
  }

  const [songCanPlay, setSongCanPlay] = useState(false)

  useEffect(() => {
    const t = audio.current
    playing ? t.play() : t.pause()
  }, [playing])

  function canPlay() {
    setSongCanPlay(playing)
    // 保存播放记录
  }
  function onError() {
    // 
  }
  function onTimeUpdate() {}

  function onEnded() {}

  function togglePlaying() {
    // if (!songCanPlay) {
    //   return
    // }
    console.log(playing);
    togglePlayingStatus(!playing)
    // 歌词同步暂停
    // if () {}
  }

  useEffect(() => {
    setShow(true)
  }, [])
  return (
    <div className='Mu-player'>
      <CSSTransition
        in={show}
        timeout={0}
        unmountOnExit
        onEntered={enter}
        appear={true}
      >
        <div className='Mu-player--normal'>
          <div className='background'>
            <img src={bg} width='100%' height='100%' alt='' />
          </div>
          <div className='top'>
            <div className='title'>
              <div className='title-btn' onClick={() => history.goBack()}>
                <i className='iconfont icon-fanhui'></i>
              </div>
              <div className='name'>天下有情人</div>
              <div className='title-btn'></div>
            </div>
            <p className='singer'>李克勤 / 周深</p>
          </div>
          <div className='middle'>
            <div className='middle-l'>
              <div className='cd-wrapper' ref={cdRef}>
                <div className='cd play'>
                  <img src={bg} alt='' />
                </div>
              </div>
              <div className='playing-lyric-wrapper'>
                <div className='playing-lyric'>无歌词数据</div>
              </div>
            </div>
          </div>
          <div className='bottom'>
            <div className='dot-wrapper'>
              <div className='dot active'></div>
              <div className='dot'></div>
            </div>
            <div className='progress-wrapper'>
              <span className='time time-l'></span>
              <div className='progress-bar-wrapper'>
                <div className='my-progress-bar'>
                  <div className='bar-inner'>
                    <div className='progress'></div>
                    <div
                      className='progress-btn-wrapper'
                      style={{ transform: 'translate3d(0, 0, 0)' }}
                    >
                      <div className='progress-btn'></div>
                    </div>
                  </div>
                </div>
              </div>
              <span className='time time-r'>04:35</span>
            </div>
            <div className='operators'>
              <div className='icon infinite'>
                <i className='iconfont icon-mode'></i>
              </div>
              <div className='icon back'>
                <i className='iconfont icon-prev'></i>
              </div>
              <div className='icon play'>
                <i onClick={togglePlaying} className={['iconfont big', songCanPlay ? 'icon-pause' : 'icon-play'].join(' ')}></i>
              </div>
              <div className='icon next'>
                <i className='iconfont icon-next'></i>
              </div>
              <div className='icon favor'>
                <i className='iconfont icon-favor'></i>
              </div>
            </div>
          </div>
        </div>
      </CSSTransition>

      <div className='Mu-player--mini'></div>

      <audio
        ref={audio}
        src={juhao}
        onPlay={canPlay}
        onPause={canPlay}
        onError={onError}
        onTimeUpdate={onTimeUpdate}
        onEnded={onEnded}
      >
        Your browser does not support the audio element.
      </audio>
    </div>
  )
}

const mapStateToProps = (state) => {
  return {
    playing: state.player.playing
  }
}

export default connect(
  mapStateToProps,
  { togglePlayingStatus }
)(Player)