import React, { useEffect } from 'react'
import MuIndexList from '../../components/MuIndexList'
import './index.scss'
import { querySingerList } from '../../services/music'

function Singers() {
  useEffect(() => {
    querySingerList()
  }, [])
  return (
    <div className="Mu-singers">
      <MuIndexList/>
    </div>
  )
}

export default Singers