// import Loadable from 'react-loadable'

import Home from '../views/Home'
import User from '../views/User'
import Singers from '../views/Singers'
import SortCards from '../views/SortCards'
import Search from '../views/Search'
import RankList from '../views/SortCards/list'
import Player from '../views/Player'

const routes = [
  {
    path: '/',
    component: Home,
    exact: true,
    meta: { role: ['auth'] }
  },
  {
    path: '/singers',
    component: Singers
  },
  {
    path: '/user',
    component: User,
    exact: false
  },
  {
    path: '/rank',
    component: SortCards,
    children: [
      //
    ]
  },
  {
    path: '/rank/:id',
    component: RankList
  },
  {
    path: '/search',
    component: Search
  },
  {
    path: '/player',
    component: Player
  }
]

export default routes