import React, { useState, useEffect, useRef } from 'react'
import BScroll from 'better-scroll'
import { connect } from 'react-redux'
import { toggleIsReachTop } from '../../store/actionCreator'
import { useHistory } from 'react-router'

const list = [
  { songname: '下山', singer: '要不要买菜', album: '下山' },
  { songname: '句号', singer: 'G.E.M. 邓紫棋', album: '摩天动物园' },
  { songname: '透明', singer: 'G.E.M. 邓紫棋', album: '摩天动物园' },
  { songname: '麻雀', singer: '李荣浩', album: '麻雀' },
  { songname: '牧马城市', singer: '毛不易', album: '老男孩 电视剧原声带' },
  { songname: '桥边姑娘', singer: '海伦', album: '桥边姑娘' },
  { songname: '你的答案', singer: '阿冗', album: '你的答案' },
  { songname: '火红的萨日朗', singer: '要不要买菜', album: '火红的萨日朗' },
  { songname: '嚣张', singer: 'en', album: '嚣张' } 
]

function SongList(props) {
  const listEl = useRef()
  const history = useHistory()
  const [scroller, setScroller] = useState(null)

  useEffect(() => {
    let temp = new BScroll(listEl.current, {
      probeType: 3,
      bounce: false
    })
    setScroller(temp)
    temp.disable()

    temp.on('scroll', (e) => {
      // toggleIsReachTop(e.y > 0 ? true : false)
    })
    // temp.disable()
    // 根据传入的canScroll参数，
    // 让滚动对象可滚动或不可滚动
    // scroller && scroller.disable()
    // scroller && (props.canScroll ? scroller.enable() : scroller.disable())
    return () => {
      // 
    };
  }, [])

  return (
    <div className="Mu-song-list" ref={listEl}>
      <ul>
        {
          list.map((item, index) => (
            <li className="Mu-song-item" key={index} onClick={() => history.push('/player')}>
              <span>{index + 1}</span>
              <div className="Mu-song-info">
                <p className="song-name">{item.songname}</p>
                <p>{item.singer} - {item.album}</p>
              </div>
            </li>
          ))
        }
      </ul>
    </div>
  )
}


export default connect(
  null,
  { toggleIsReachTop }
)(SongList)