import React, { useState, useEffect } from 'react'
import './shortcut.scss'
import { getAlphabet } from '../../utils/utils'

/**
 * 获取滑动选中的索引对象
 * @param {Event} e 事件对象
 */
function getActiveDot(e) {
  const offsetTop = e.touches[0].clientY
  const dots = document.querySelectorAll('.Mu-singers-dot')
  let temp = null
  for (const dot of dots) {
    const { bottom } = dot.getBoundingClientRect()
    if (offsetTop < bottom) {
      temp = dot
      break
    }
  }
  return temp
}

function handleMove(e) {
  const target = getActiveDot(e)
  return (target && target.innerText) || ''
}

function ShortCut(props) {
  const list = ['热', ...getAlphabet()]
  const [el, setEl] = useState(null)
  const [label, setLabel] = useState('')

  useEffect(() => {
    el && el.addEventListener('touchmove', (e) => {
      const text = handleMove(e)
      setLabel(text)
      props.syncLable(text)
    })
    el && el.addEventListener('touchend', () => {
      setLabel('')
    })
    return () => {
      // el && el.removeEventListener('touchmove', handleMove)
    };
  }, [el, props])

  return (
    <div className="Mu-singers-shortcut">
      <ul ref={ref => setEl(ref)}>
        {
          list.map((item, index) => (
            <li className={["Mu-singers-dot", label === item ? "Mu-singers-dot--active" : ""].join(' ')} data-index={index} key={index}>{item}</li>
          ))
        }
      </ul>
    </div>
  )
}
export default ShortCut