import pinyin from 'pinyin'
/**
 * 将汉字排序，并根据拼音分组
 * { 'a': [], 'b': [] }
 */
export function convert2pinyin(list) {
  sortByPinyin(list)
  const temp = {}
  for (const v of list) {
    const name = pinyin(v.name)[0][0]
    const first_letter = name.slice(0, 1).toLocaleLowerCase()
    if (temp[first_letter]) {
      temp[first_letter].push(v)
    } else {
      temp[first_letter] = [v]
    }
  }
  return temp
}

/**
 * 根据拼音排序
 */
export function sortByPinyin(list) {
  return list.sort((prev, cur) => {
    // 格式: 汉字 => [[''], ['']]
    const p_name = pinyin(prev.name)[0][0]
    const c_name = pinyin(cur.name)[0][0]
    return p_name.localeCompare(c_name)
  })
}

/**
 * 计算元素距离上级可滚动区域的高度
 */
export function calcOffsetOnScreen(el) {
  const p = findVisualView(el)
  const { top: p_top } = p.getBoundingClientRect()
  const { top } = el.getBoundingClientRect()
  return top - p_top
}
/**
 * 查找离el元素最近的，可滚动的元素
 * @param {HTMLElement} el HTML元素节点
 */
function findVisualView (el) {
  let parent = el.parentNode
  while(parent) {
    let overflow = window.getComputedStyle(parent)['overflow-y']
    if (overflow === 'hidden') {
      break
    }
    parent = parent.parentNode
  }
  return parent
}

/**
 * 快速生成26个字母表
 */
export function getAlphabet () {
  const alphabet = []
  for (let i = 0; i < 26; i++) {
    alphabet.push(String.fromCharCode(65 + i))
  }
  return alphabet
}
/**
 * 判断是否不是空对象
 * @param {Any} o 一个对象
 */
export function notEmpty(o) {
  for (const i in o) {
    return true
  }
  return false
}
/**
 * 
 * @param {Object} query 查询参数
 */
export function concatQueryStr(query = {}) {
  let str = ''
  const keys = Object.keys(query)
  for (let i = 0, len = keys.length; i < len; i++) {
    const t = query[keys[i]]
    if (t === undefined || t === null) {
      continue
    }
    if (i !== 0) {
      str += '&'
    }
    str += `${keys[i]}=${t}`
  }
  return str
}