import React from 'react'
import { useHistory } from 'react-router'
import MuIcon from '../MuIcon'
import Logo from '../../logo.svg'
import './index.scss'

function MuHeader(props) {
  const history = useHistory()

  function back() {
    history.goBack()
  }
  return (
    <header
      className={['Mu-header', props.fixed ? 'Mu-header--fixed' : ''].join(' ')}
    >
      {props.isBack ? (
        <>
          <div className='Mu-header-btn Mu-header-left' onClick={back}>
            <i className='iconfont icon-fanhui'></i>
          </div>
          <p className='Mu-header-text'>{props.title}</p>
          <div className='Mu-header-btn'></div>
        </>
      ) : (
        <>
          <h3 className='Mu-title'>
            <MuIcon src={Logo} />
            {props.title}
          </h3>
        </>
      )}
    </header>
  )
}

export default MuHeader
