import React, { useState, useEffect } from 'react'
import BScroll from 'better-scroll'
import TagFixed from './tag-fixed'
import ShortCut from './shortcut'
import { convert2pinyin, calcOffsetOnScreen, notEmpty } from '../../utils/utils'
import avatar from '../../assets/images/avatar.png'

const list = [
  { name: '林俊杰' },
  { name: '许嵩' },
  { name: '周杰伦' },
  { name: '陈奕迅' },
  { name: '李荣浩' },
  { name: 'TFBoys' },
  { name: '薛之谦' },
  { name: 'BIGBANG' },
  { name: '本兮' },
  { name: '陈小春' },
  { name: '陈翔' },
  { name: '蔡健雅' },
  { name: '李易峰' },
  { name: '林宥嘉' }
]

/**
 * 分别获取当前的panel以及下一个panel的节点
 */
function getTitles() {
  let index = 0
  const titles = document.querySelectorAll('.Mu-singers-title')
  for (let i = 0; i < titles.length; i++) {
    if (calcOffsetOnScreen(titles[i]) > 0) {
      index = i
      break
    }
  }
  return [titles[index - 1], titles[index]]
}

/**
 * 处理滚动事件，返回stick需要偏移的值，以及文字
 * @param {Event} e 事件对象
 */
function handleScroll() {
  const [cur, next] = getTitles()
  const offset = calcOffsetOnScreen(next)
  return [offset <= 30 ? offset - 30 : 0, cur && cur.innerText]
}

function MuIndexList() {
  const [singers, setsingers] = useState({})
  const [offset, setoffset] = useState(0)
  const [tagstatus, settagstatus] = useState(true)
  const [label, setLabel] = useState('')
  const [scroller, setScroller] = useState(null)
  let el = null
  function handleClick(t) {
    const titles = document.querySelectorAll('.Mu-singers-title')
    // console.log(t);
    for (const title of titles) {
      if (title.innerText.toLocaleLowerCase() === t.toLocaleLowerCase()) {
        scroller.scrollToElement(title)
        break
      }
    }
  }
  useEffect(() => {
    setsingers(convert2pinyin(list))
    let temp = new BScroll(el, {
      probeType: 3
    })
    setScroller(temp)

    notEmpty(singers) && setLabel(Object.keys(singers)[0])
    
    temp.on('scroll', e => {
      // 当列表在顶部时，下拉隐藏tag
      settagstatus(e.y > 0 ? false : true)
      const [offset, name] = handleScroll()
      setoffset(offset)
      setLabel(name)
    })
  }, [el])

  return (
    <div className='Mu-singers-wrapper' ref={ref => (el = ref)}>
      <ul className='Mu-singers-main'>
        {Object.keys(singers).map((letter, index) => (
          <li className='Mu-singers-group' key={index}>
            <h2 className='Mu-singers-title'>{letter.toLocaleUpperCase()}</h2>
            <ul>
              {singers[letter].map((singer, idx) => (
                <li className='Mu-singers-item' key={idx}>
                  <img src={avatar} alt='头像' />
                  {singer.name}
                </li>
              ))}
            </ul>
          </li>
        ))}
      </ul>
      { tagstatus ? <TagFixed top={offset} stickName={label} /> : null }
      <ShortCut syncLable={handleClick} />
    </div>
  )
}

export default MuIndexList
