import React, { useState, useEffect, useRef } from 'react'
import { connect } from 'react-redux'
import { addTodo } from '../../store/actionCreator'
import MuHeader from '../../components/MuHeader'
import SongList from './song-list'
import bg from '../../assets/images/bg.jpeg'

let initPosY = 0, initBgHeight = 0

function getRectHeight(el) {
  const { top, bottom, height } = el.current.getBoundingClientRect()
  return height || (bottom - top)
}

function RankList(props) {
  const [ratio, setRatio] = useState(1)
  const [blur, setBlur] = useState(0)
  const [offset, setOffset] = useState(0)

  const layerRef = useRef()
  const bgRef = useRef()

  useEffect(() => {
    // console.log(layerRef);
  }, [])

  function handleStart(evt) {
    const { pageY } = evt.touches[0]
    initPosY = pageY
    initBgHeight = getRectHeight(bgRef)
  }

  function handleMove(evt) {
    const { pageY } = evt.touches[0]
    const scrollOffset = pageY - initPosY
    // 设置背景图片的缩放比
    setRatio(scrollOffset / 1400 + 1)
    setOffset(getRectHeight(bgRef) - initBgHeight)
  }
  function handleEnd(evt) {
    setRatio(1)
    setOffset(0)
  }

  return (
    <div className='Mu-rank-page'>
      <MuHeader isBack fixed title='巅峰·流行指数' />
      <div
        className='Mu-rank-bg'
        style={{
          backgroundImage: 'url(' + bg + ')',
          paddingTop: '70%',
          height: '0',
          zIndex: 0,
          transform: 'scale('+ ratio +')'
        }}
        ref={bgRef}
      >
        <div className="Mu-rank-play-all">
          <button className="Mu-rank-play-btn">
            <i className="iconfont icon-icon-7"></i>
            随机播放全部
          </button>
        </div>
        <div className="Mu-rank-bg-filter" style={{ backdropFilter : `blur(${blur}px)` }}></div>
      </div>
      <div
        className='Mu-rank-layer'
        ref={layerRef}
        onTouchStart={handleStart}
        onTouchMove={handleMove}
        onTouchEnd={handleEnd}
        style={{ transform: 'translateY('+ offset +'px)' }}
      >
        <SongList canScroll={true} />
      </div>
    </div>
  )
}

const mapStateToProps = (state) => {
  return {
    content: state.test.content
  }
}

export default connect(
  mapStateToProps,
  { addTodo }
)(RankList)
